.case-single {
  background: transparent;
}

.case-single__first-screen {
  min-height: 600px;
  height: 180vh;
  background: transparent;
  display: flex;
  padding-top: 12.5%;
}

.case-single__main-screen {
  min-height: 800px;
  background-color: red;
}

.case-single__section {
  flex-wrap: wrap;
}

.parallax-slider {
  transition: filter 0.5s;
}

.parallax-slider.blur {
  filter: blur(10px);
}
